<template>
    <div class="about">
        <h1>{{ id ? '编辑' : '新建' }}英雄</h1>
        <el-form label-width="120px" @submit.native.prevent="save">
            <el-tabs value="basic" type="border-card">
                <el-tab-pane label="基本信息" name="basic">
                    <el-form-item label="名称">
                        <el-input v-model="model.name"></el-input>
                    </el-form-item>
                    <el-form-item label="称号">
                        <el-input v-model="model.title"></el-input>
                    </el-form-item>
                    <el-form-item label="头像">
                        <el-upload
                        class="avatar-uploader"
                        :action="uploadUrl"
                        :headers="getAuthHeader()"
                        :show-file-list="false"
                        :on-success="res => $set(model,'avatar',res.url)"
                        >
                        <img v-if="model.avatar" :src="model.avatar" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>    
                        </el-upload>
                    </el-form-item>
                    <el-form-item label="banner">
                        <el-upload
                        class="avatar-uploader"
                        :action="uploadUrl"
                        :headers="getAuthHeader()"
                        :show-file-list="false"
                        :on-success="res => $set(model,'banner',res.url)"
                        >
                        <img v-if="model.banner" :src="model.banner" class="banner">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>    
                        </el-upload>
                    </el-form-item>
                    <el-form-item label="类型">
                        <el-select filterable v-model="model.categories" multiple>
                            <el-option v-for="item of categories" :key="item._id"
                            :label="item.name"
                            :value="item._id"
                            ></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="难度">
                        <el-rate style="margin-top:0.6rem;" show-score :max="9" v-model="model.scores.difficult"></el-rate>
                    </el-form-item>
                    <el-form-item label="技能">
                        <el-rate style="margin-top:0.6rem;" show-score :max="9" v-model="model.scores.skills"></el-rate>
                    </el-form-item>
                    <el-form-item label="攻击">
                        <el-rate style="margin-top:0.6rem;" show-score :max="9" v-model="model.scores.attack"></el-rate>
                    </el-form-item>
                    <el-form-item label="生存">
                        <el-rate style="margin-top:0.6rem;" show-score :max="9" v-model="model.scores.survive"></el-rate>
                    </el-form-item>
                    <el-form-item label="顺风出装">
                        <el-select filterable v-model="model.items1" multiple>
                            <el-option v-for="item of items" :key="item._id"
                            :label="item.name"
                            :value="item._id"
                            ></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="逆风出装">
                        <el-select filterable v-model="model.items2" multiple>
                            <el-option v-for="item of items" :key="item._id"
                            :label="item.name"
                            :value="item._id"
                            ></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="使用技巧">
                        <el-input type="textarea" v-model="model.usageTips"></el-input>
                    </el-form-item>
                    <el-form-item label="对抗技巧">
                        <el-input type="textarea" v-model="model.battleTips"></el-input>
                    </el-form-item>
                    <el-form-item label="团战思路">
                        <el-input type="textarea" v-model="model.teamTips"></el-input>
                    </el-form-item>
                </el-tab-pane>
                <el-tab-pane label="技能" name="skills">
                    <el-button size="small" @click="model.skills.push({})"><i class="el-icon-plus"></i> 添加技能</el-button>
                    <el-row >
                        <el-col :md="12" v-for="(item,i) of model.skills" :key="i">
                            <el-form-item label="名称">
                                <el-input v-model="item.name"></el-input>
                            </el-form-item>
                            <el-form-item label="图标">
                                <el-upload
                                class="avatar-uploader"
                                :action="uploadUrl"
                                :headers="getAuthHeader()"
                                :show-file-list="false"
                                :on-success="res => $set(item,'icon' , res.url)"
                                >
                                <img v-if="item.icon" :src="item.icon" class="avatar">
                                <i v-else class="el-icon-plus avatar-uploader-icon"></i>    
                                </el-upload>
                            </el-form-item>
                            <el-form-item label="冷却值">
                                <el-input v-model="item.delay"></el-input>
                            </el-form-item>
                            <el-form-item label="消耗">
                                <el-input v-model="item.cost"></el-input>
                            </el-form-item>
                            <el-form-item label="描述">
                                <el-input v-model="item.description" type="textarea"></el-input>
                            </el-form-item>
                            <el-form-item label="小提示">
                                <el-input v-model="item.tips" type="textarea"></el-input>
                            </el-form-item>
                            <el-form-item >
                                <el-button type="danger" @click="model.skills.splice(i,1)">删除</el-button>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-tab-pane>
                <el-tab-pane label="最佳搭档" name="partners">
                    <el-button size="small" @click="model.partners.push({})"><i class="el-icon-plus"></i> 添加搭档</el-button>
                    <el-row >
                        <el-col :md="12" v-for="(item,i) of model.partners" :key="i">
                            <el-form-item label="名称">
                                <el-select filterable v-model="item.hero">
                                    <el-option v-for="hero in heroes" 
                                    :key="hero._id"
                                    :label="hero.name"
                                    :value="hero._id"
                                    ></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="描述">
                                <el-input v-model="item.description" type="textarea"></el-input>
                            </el-form-item>
                            <el-form-item >
                                <el-button type="danger" @click="model.partners.splice(i,1)">删除</el-button>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-tab-pane>
            </el-tabs>
            
            <el-form-item style="margin-top:1rem;">
                <el-button type="primary" native-type="submit">保存</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
export default {
    props:{
        id:{}
    },
    data(){
        return {
            items:[],
            categories:[],
            model:{
                name:'',
                avatar:'',
                scores:{
                    difficult:0
                },
                skills:[],
                partners:[]
            },
        }
    },
    methods:{
        async save(){
            if(this.id){
                //更新
                await this.$http.put(`rest/heroes/${this.id}`,this.model)
                //新建
            }else{
                await this.$http.post('rest/heroes',this.model)
            }
            //this.$router.push('/heroes/list')
            this.$message({
                type:'success',
                message:'保存成功'
            })
        },
        async fetch(){
            const res = await this.$http.get(`rest/heroes/${this.id}`)
            this.model = Object.assign({},this.model,res.data)
        },
        async fetchCategories(){
            const res = await this.$http.get(`rest/categories`)
            this.categories = res.data
        },
        async fetchItems(){
            const res = await this.$http.get(`rest/items`)
            this.items = res.data
        },
        async fetchHeroes(){
            const res = await this.$http.get(`rest/heroes`)
            this.heroes = res.data
        }
        // afterUpload(res){
        //     console.log(res);
        //     // vue中响应数据的问题
        //     //this.$set(this.model,'avatar',res.url)
        //     this.model.avatar = res.url
        // }
    },
    created(){
        this.fetchItems()
        this.fetchCategories()
        this.fetchHeroes()
        this.id && this.fetch()
    }
}
</script>

